<template>
  <div :class="{'signup-success-container': isPc, 'signup-success-container-mb': !isPc}">
    <div class="header-header" :style="{backgroundImage: 'url(' + $assetsUrl + 'user/signup-success-bg.svg)'}">
      <i class="success-icon" :style="{backgroundImage: 'url(' + $assetsUrl + 'user/order-free-success.svg)'}"></i>
      <p class="success-title">試用訂單已收到</p>
    </div>
    <div class="card-detail">
      <div class="header">
        <p class="header-title">請去郵箱確認。</p>
        <p class="header-tips">如未收到，請查看垃圾箱或
          <a-button v-if="loading" type="link" :loading="loading"></a-button>
          <span v-if="!loading && !resendTip" @click="resendEmail()">重發</span>
          <span v-if="resendTip" style="color: #768392">已發送({{secondNum}})</span>
        </p>
        <p class="header-tips-footer">訂單完成前，請勿關閉本頁。如需幫助，請聯繫客服。</p>
      </div>
      <div class="package-list">
        <p class="package-title">訂單詳情</p>
        <div class="list-item">
          <p>帳號</p>
          <p>{{packageInfo.account}}</p>
        </div>
        <div class="list-item">
          <p>套餐</p>
          <p>{{packageInfo.packages}}</p>
        </div>
        <div class="list-item">
          <p>訂閱週期</p>
          <p>{{packageInfo.cycle}}</p>
        </div>
        <div class="list-item">
          <p>原價</p>
          <p>{{packageInfo.originalPrice}} USD</p>
        </div>
        <div class="list-item">
          <p>優惠</p>
          <p class="reduce">- {{packageInfo.reduce}} USD</p>
        </div>
        <div class="total">
          <p>總額</p>
          <p>{{packageInfo.preferentialPrice}} USD</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

  export default {
    components: {},
    data() {
      return {
        loading: false,
        resendTip: false,
        secondNum: 60,
        packageInfo: {
          account: String,
          packages: String,
          cycle: String,
          originalPrice: String,
          reduce: String,
          preferentialPrice: String,
        },
        isPc: this.$isPc()
      };
    },
    created() {
      this.packageInfo.account = this.$route.params.account;
      this.packageInfo.packages = this.$route.params.packages;
      this.packageInfo.cycle = this.$route.params.cycle;
      this.packageInfo.originalPrice = this.$route.params.originalPrice;
      this.packageInfo.reduce = this.$route.params.reduce;
      this.packageInfo.preferentialPrice = this.$route.params.preferentialPrice;

      // go back to sign up page if lost the email
      if (!this.packageInfo.account) {
        this.$router.push('signup')
      }
    },
    methods: {
      resendEmail() {

        this.loading = true;
        this.$http.post(`${process.env.VUE_APP_SERVER_URL}/api/v2/users`, {
          'email': this.packageInfo.account,
          'affid': this.$route.query['aff'] || localStorage.getItem('aff') || ''
        })
          .then(
            (data) => {
              this.loading = false;
              this.resendTip = true;
              let test = setInterval(() => {
                this.secondNum--;
                if (this.secondNum === 0) {
                  window.clearInterval(test);
                  this.resendTip = false;
                  this.secondNum = 60;
                }
              }, 1000);
              // if (data && (data.status === 200 || data.status === 201)) {
              //
              // } else {
              //   this.errorDisplay = true;
              //   this.errorDescription = '出現錯誤，請稍後重試';
              // }
            });
      },
    }
  };
</script>

